import React from 'react';
import './styles.css';
import {Link} from 'react-router-dom';
import axios from 'axios';
const BranchCreate = ()=>{
    const handleSubmit = (e)=>{
        e.preventDefault();
        const emailInput = document.getElementById('email')
        const passwordInput = document.getElementById('password')
        const nameInput = document.getElementById('name')
        axios.post('http://127.0.0.1:8000/branch/branches/',{email:emailInput.value,password:passwordInput.value,name:nameInput.value},{ headers: {Authorization: 'Bearer ' + window.localStorage.getItem('token')}})
        .then(resp=>{
            // window.localStorage.setItem('token', resp.data.access);
            window.location = '/admin'
            // history.push('/')
        })   
        .catch(errors =>{
            console.log(errors)
            const errorMessage = document.getElementById('error-message');
            errorMessage.innerHTML = '=> Invalid Username and password'
        })
    }
    return (
        <div className="neomorphism-effect signin-form">
            <center>
                <h2>Create branch</h2>
            </center>
                <form onSubmit={e=>handleSubmit(e)}>
                    <label>Branch Email</label>
                    <center><input type="email" placeholder="Email" id="email" /></center>
                    <label>Branch Password</label>
                    <center><input type="password" placeholder="password" id="password" /></center>
                    <label>Branch Name</label>
                    <center><input type="text" placeholder="Name" id="name" /></center>
                    <center><input type="submit" value="Create Branch" className="submit-btn"/></center>
                </form>
                <Link to="/admin" className="back-btn"> Back</Link>
                <p id="error-message"></p>
        </div>
    )
}
export default BranchCreate;