import React from 'react';
import Home from './components/Home';
import {Route,Switch} from 'react-router-dom';
import SignIn from './components/SignIn';
import Dashborad from './components/Dashboard';
import CreateBrnach from './components/admin/branchCreate'
import AdminDashboard from './components/admin/Dashboard'
import CreateTruck from './components/admin/CreateTruck'
import BranchDashboard from './components/employee/Dashboard'
// import SignUp from './components/SignUp'
function App() {
  return (
    <Switch>
      <Route path='/' exact >
        <Home />
      </Route>
      <Route path='/login' exact >
        <SignIn />
      </Route>
      <Route path='/dashboard/:user_type' exact>
        <Dashborad />
      </Route>
      <Route path='/admin' exact>
        <AdminDashboard />
      </Route>
      <Route path='/admin/branch-create' exact >
        <CreateBrnach />
      </Route>
      <Route path="/admin/truck-assign" exact >
        <CreateTruck />
      </Route>
      <Route path="/employee" exact>
        <BranchDashboard />
      </Route>
      {/* <Route path="/signup" exact >
        <SignUp />
      </Route> */}
    </Switch>
  );
}

export default App;
