import React from 'react';
import './styles/SignIn.css';
import {Link} from 'react-router-dom';
import axios from 'axios'
const SignIn = ()=>{
    const handleSubmit = (e)=>{
        e.preventDefault();
        const emailInput = document.getElementById('email')
        const passwordInput = document.getElementById('password')
        axios.post('http://127.0.0.1:8000/api/token/',{email:emailInput.value,password:passwordInput.value})
        .then(resp=>{
            window.localStorage.setItem('token', resp.data.access);
            window.location = '/'
            // history.push('/')
        })   
        .catch(errors =>{
            console.log(errors)
            const errorMessage = document.getElementById('error-message');
            errorMessage.innerHTML = '=> Invalid Username and password'
        })
    }
    return (
        <div className="neomorphism-effect signin-form">
            <center>
                <h2>SignIn To Your Account</h2>
            </center>
                <form onSubmit={e=>handleSubmit(e)}>
                    <label>Your Email</label>
                    <center><input type="email" placeholder="Email" id="email" /></center>
                    <label>Your Password</label>
                    <center><input type="password" placeholder="password" id="password" /></center>
                    <center><input type="submit" value="SignIn" className="submit-btn"/></center>
                </form>
                <Link to="/" className="back-btn"> Back</Link>
                <p id="error-message"></p>
        </div>
    )
}
export default SignIn;