import React,{useState,useEffect} from 'react';
import axios from 'axios';
const Truck = ()=>{
    const [trucks,setTrucks] = useState('')
    useEffect(()=>{
        if(window.localStorage.getItem('token')){
            axios.get('http://127.0.0.1:8000/branch/branch-trucks/',{ headers: {Authorization: 'Bearer ' + window.localStorage.getItem('token')}})
            .then(resp=>{
                console.log(resp.data)
                setTrucks(resp.data)
            })
            .catch(errors =>{
                console.log("errors")
            })
        }
    },[])
    const renderData = ()=>{
        return (
            <table>
                <tbody>
                    <tr>
                        <th>Id</th>
                        <th>Truck Name</th>
                        <th>Branch Name</th>
                    </tr>
                {trucks.map((truck)=>{
                    return (
                        <tr key={truck.id}>
                            <td>{truck.id}</td>
                            <td>{truck.name}</td>
                            <td>{truck.branch.name}</td>
                        </tr>
                        )
                })}
                </tbody>
            </table>
        )
    }
    return (
        <div className="center-content">
        {trucks == '' ? <div style={{'marginTop':'10px'}}>No Trucks</div> : renderData()}
    </div>
    )
}
export default Truck;