import React from 'react';
import './styles/Header.css';
import {Link} from 'react-router-dom';
const Header =(props)=>{
    console.log(props)
    return (
        <div className="navbar-container">
            <div className="container">
                <nav>
                    <div className="navbar-logo">
                        Truck Wala
                    </div>
                    <div>
                        <a className="nav-link" href="#status">Status</a>
                        {props.info === '' ? null : <Link to={`/dashboard/${props.info.user_type}`} className="nav-link">dashboard</Link>}
                        {props.info === '' ? <Link to="/login"><span className="nav-link login-btn">Login</span></Link> :
                            <span className="nav-link login-btn" onClick={()=>{
                                window.localStorage.removeItem('token')
                                window.location = '/'
                            }}>Logout</span>
                        }
                        
                    </div>
                </nav>
            </div>
        </div>
    )
};
export default Header;