import React,{useState,useEffect} from 'react';
import axios from 'axios';
const Trips = ()=>{
    const [trips,setTrips] = useState('')
    useEffect(()=>{
        if(window.localStorage.getItem('token')){
            axios.get('http://127.0.0.1:8000/branch/trips/',{ headers: {Authorization: 'Bearer ' + window.localStorage.getItem('token')}})
            .then(resp=>{
                console.log(resp.data)
                setTrips(resp.data)
            })
            .catch(errors =>{
                console.log("errors")
            })
        }
    },[])
    const getFormattedDate = date=>{
        const date_array = date.split('-')
        return date_array[0]+ '-'+date_array[1]+'-'+date_array[2][0]+date_array[2][1];
    }
    return (
        <div style={{'marginTop':'20px'}}>
            <center><h1>Previous Trips</h1></center>
            {trips === '' ? <div>Loading..</div>:<div  className="neomorphism-effect tracking-details">
                <table>
                        <tbody>
                            <tr>
                                <td>Source</td>
                                <td>Destination</td>
                                <td>Status</td>
                                <td>Truck Name</td>
                                <td>Storage</td>
                                <td>Time</td>
                            </tr>
                            {trips.map(trip=>{
                                return (<tr>
                                    <td style={{'color':'grey'}}>{trip.source.name}</td>
                                    <td style={{'color':'grey'}}>{trip.destination.name}</td>
                                    <td style={{'color':'grey'}}>{trip.status}</td>
                                    <td style={{'color':'grey'}}>{trip.truck.name}</td>
                                    <td style={{'color':'grey'}}>{trip.space} kg</td>
                                    <td style={{'color':'grey'}}>{getFormattedDate(trip.time)}</td>
                                </tr>)
                            })}
                        </tbody>
                    </table>
            </div>}
        </div>
    )
}
export default Trips;