import React,{useState} from 'react';
import './styles.css';
import Work from './Work';
import Truck from './Trucks';
import Trips from './Trips'
import {Link} from 'react-router-dom';
const Dashboard = ()=>{
    const [category,setCategory] = useState('work')
    const renderCategory = () =>{
        if(category === 'work'){
            return <Work />
        }
        else if(category === 'trucks'){
            return <Truck />
        }
        else if(category === 'trips'){
            return <Trips />
        }
    }
    const changeCategory = (cat,e) =>{
        const allDivs = document.getElementsByClassName('tab-btn')
        for (var i = 0; i < allDivs.length; i++) {
            allDivs[i].classList.remove('active');
        }
        e.currentTarget.classList.add('active')
        setCategory(cat)
    }
    return (
        <div className="dashboard">
            <Link to="/"><button className="dashboard-back">Back</button></Link>
            <h1>Branch Dashborad</h1>
            <div className="tabs">
                <button className="tab-btn active" onClick={(e)=>changeCategory('work',e)}>Work</button>
                <button className="tab-btn" onClick={(e)=>changeCategory('trips',e)}>Trips</button>
                <button className="tab-btn" onClick={(e)=>changeCategory('trucks',e)}>Trucks</button>
            </div>
                <div className="dashboard-content">
                    {renderCategory()}
                </div>
        </div>
    )
}
export default Dashboard;
