import React,{useState,useEffect} from 'react';
import './styles.css';
import axios from 'axios';
const Work = ()=>{
    const [branches,setBranches] = useState('')
    useEffect(()=>{
        if(window.localStorage.getItem('token')){
            axios.get('http://127.0.0.1:8000/branch/other-branches/',{ headers: {Authorization: 'Bearer ' + window.localStorage.getItem('token')}})
            .then(resp=>{
                setBranches(resp.data)
            })
            .catch(errors =>{
                console.log("errors")
            })
        }
    },[])
    const handleSubmit = (e)=>{
        e.preventDefault();
        const branchId = document.getElementById('branches')
        const nameInput = document.getElementById('name')
        axios.post('http://127.0.0.1:8000/branch/add-work/',{id:branchId.value,weight:nameInput.value},{ headers: {Authorization: 'Bearer ' + window.localStorage.getItem('token')}})
        .then(resp=>{
            // window.localStorage.setItem('token', resp.data.access);
            // window.location = '/admin'
            // history.push('/')
            const errorMessage = document.getElementById('error-message');
            let htmlString =''
            if(resp.data.id)
            {
                htmlString = '** '+resp.data.message+" Your Tracking id is "+resp.data.id + " **"
            }
            else{
                htmlString = '** '+resp.data.message+ " **"
            }
            errorMessage.innerHTML =htmlString
        })   
        .catch(errors =>{
            console.log(errors)
            const errorMessage = document.getElementById('error-message');
            errorMessage.innerHTML = '=> Something Went Wrong'
        })
    }
    return (
        <div className= "signin-form" style={{'marginTop':'30px'}}>
            <center>
                <h2>Enter Luggage Details</h2>
            </center>
                <form onSubmit={e=>handleSubmit(e)}>
                    <label>Select Branch</label>
                    <center><select id="branches">
                    {branches !=='' ? branches.map((branch)=>{
                        return (
                            <option value={branch.id}>{branch.name}</option>
                            )
                    }):null}
                    </select></center>
                    <label>Weight </label>
                    <center><input type="number" placeholder="Weight in Kg" id="name" /></center>
                    <center><input type="submit" value="Assign Truck" className="submit-btn"/></center>
                </form>
                <center><p id="error-message"></p></center>
        </div>
    ) 
}
export default Work;