import React,{useState,useEffect} from 'react';
import './styles.css';
import axios from 'axios';
import {Link} from 'react-router-dom'
const Branches = ()=>{
    const [branches,setBranches] = useState('')
    useEffect(()=>{
        if(window.localStorage.getItem('token')){
            axios.get('http://127.0.0.1:8000/branch/branches/',{ headers: {Authorization: 'Bearer ' + window.localStorage.getItem('token')}})
            .then(resp=>{
                setBranches(resp.data)
            })
            .catch(errors =>{
                console.log("errors")
            })
        }
    },[])
    const deleteBranch = (id)=>{
        axios.delete(`http://127.0.0.1:8000/branch/branch/${id}`,{ headers: {Authorization: 'Bearer ' + window.localStorage.getItem('token')}})
            .then(resp=>{
                window.location = '/admin'
            })
            .catch(errors =>{
                console.log("errors")
            })
    }
    const renderData = ()=>{
        return (
            <table>
                <tbody>
                    <tr>
                        <th>Id</th>
                        <th>Branch Name</th>
                        <th></th>
                    </tr>
                {branches.map((branch)=>{
                    return (
                        <tr key={branch.id}>
                            <td>{branch.id}</td>
                            <td>{branch.name}</td>
                            <td><button className='delete-btn' onClick={()=>deleteBranch(branch.id)}>Delete</button></td>
                        </tr>
                        )
                })}
                </tbody>
            </table>
        )
    }
    return (
        <div className="center-content">
            <center><Link to="/admin/branch-create"><button className="add-btn" >Add </button></Link></center>
            {branches === '' ? <div style={{'marginTop':'10px'}}>No Branches</div> : renderData()}
        </div>
    )
}
export default Branches;