import React,{useState,useEffect} from 'react';
import Header from './Header';
import HeroImage from '../images/hero-image.png'
import './styles/Home.css';
import axios from 'axios'
const Home =()=>{
    const [userInfo,setUserInfo] = useState('')
    const [trackingDetails,setTrackingDetails] = useState('')
    useEffect(()=>{
        if(window.localStorage.getItem('token')){
            axios.get('http://127.0.0.1:8000/accounts/get_info/',{ headers: {Authorization: 'Bearer ' + window.localStorage.getItem('token')}})
            .then(resp=>{
                setUserInfo(resp.data)
            })
            .catch(errors =>{
                console.log(errors)
            })
        }
    },[])
    const getFormattedDate = date=>{
        const date_array = date.split('-')
        return date_array[0]+ '-'+date_array[1]+'-'+date_array[2][0]+date_array[2][1];
    }
    const perofrmTracking = (e)=>{
        e.preventDefault();
        const error = document.getElementById("errors");
        axios.get(`http://127.0.0.1:8000/branch/track/${document.getElementById("trackId").value}`,{ headers: {Authorization: 'Bearer ' + window.localStorage.getItem('token')}})
            .then(resp=>{
                setTrackingDetails(resp.data)
                error.innerHTML = ''
            })
            .catch(errors =>{
                setTrackingDetails('')
                error.innerHTML = 'Id Does Not Exist'
            })
    }
    return (
        <div>
            <Header info={userInfo}/>
            <div className="hero-section">
                <h1>Transfer Your <span style={{"color":"#e76247"}}>Luggage</span> easily with Us</h1>
                <div className="image-container">
                    <img src={HeroImage} alt="hero" />
                </div> 
            </div>
            <div className="divider"></div>
            <section className="container status-section" id="status">
                <center>
                    <h1>Track Your Luggage</h1>
                    <form className="status-form" onSubmit={(e)=>perofrmTracking(e)}>
                        <input type="text" placeholder="enter Tracking Id" id="trackId"/>
                        <input type="submit" className="submit-btn" />
                    </form>
                    {trackingDetails === '' ? null :<div className="neomorphism-effect tracking-details">
                        <table>
                            <tbody>
                                <tr>
                                    <td style={{'color':'grey'}}>Source</td>
                                    <td>{trackingDetails.source.name}</td>
                                </tr>
                                <tr>
                                    <td style={{'color':'grey'}}>Destination</td>
                                    <td>{trackingDetails.destination.name}</td>
                                </tr>
                                <tr>
                                    <td style={{'color':'grey'}}>Status</td>
                                    <td>{trackingDetails.status}</td>
                                </tr>
                                <tr>
                                    <td style={{'color':'grey'}}>Truck Name</td>
                                    <td>{trackingDetails.truck.name}</td>
                                </tr>
                                <tr>
                                    <td style={{'color':'grey'}}>Storage</td>
                                    <td>{trackingDetails.space} kg </td>
                                </tr>
                                <tr>
                                    <td style={{'color':'grey'}}>Time</td>
                                    <td>{getFormattedDate(trackingDetails.time)}</td>
                                </tr>
                            </tbody>
                        </table>
                    </div>}
                    <center><p style={{'color':'#e76247'}} id="errors"></p></center>
                </center>
            </section>
            <div className="divider"></div>
            <footer>
            {/* <center><p style={{'color':'#e76247'}}>We are very happy to server You!!</p></center> */}
                <div className="footer-links">
                    <div>
                        {/* <p>Connet With Us</p> */}
                        <ul>
                            <li>Facebook</li>
                            <li>Instagram</li>
                            <li>Dribble</li>
                            <li>Whatsup</li>
                        </ul>
                    </div>
                    <div className="footer-form">
                        <form onSubmit={(e)=>e.preventDefault()}>
                            <input type="text" placeholder="Email"/>
                            <input type="submit" value="Subscriibe" className="submit-btn" />
                        </form>
                    </div>
                    {/* <div>
                        <p>Services</p>
                        <ul>
                            <li>Truck</li>
                            <li>Luggage</li>
                            <li>Shifting</li>
                            <li>Transport</li>
                        </ul>
                    </div> */}
                </div>
            </footer>
        </div>
    )
};
export default Home;