import React,{useState,useEffect} from 'react';
import './styles.css';
import {Link} from 'react-router-dom';
import axios from 'axios';
const BranchCreate = ()=>{
    const [branches,setBranches] = useState('')
    useEffect(()=>{
        if(window.localStorage.getItem('token')){
            axios.get('http://127.0.0.1:8000/branch/branches/',{ headers: {Authorization: 'Bearer ' + window.localStorage.getItem('token')}})
            .then(resp=>{
                setBranches(resp.data)
            })
            .catch(errors =>{
                console.log("errors")
            })
        }
    },[])
    const handleSubmit = (e)=>{
        e.preventDefault();
        const branchId = document.getElementById('branches')
        const nameInput = document.getElementById('name')
        axios.post('http://127.0.0.1:8000/branch/trucks/',{name:nameInput.value,id:branchId.value},{ headers: {Authorization: 'Bearer ' + window.localStorage.getItem('token')}})
        .then(resp=>{
            // window.localStorage.setItem('token', resp.data.access);
            window.location = '/admin'
            // history.push('/')
        })   
        .catch(errors =>{
            console.log(errors)
            const errorMessage = document.getElementById('error-message');
            errorMessage.innerHTML = '=> Something Went Wrong'
        })
    }
    return (
        <div className="neomorphism-effect signin-form">
            <center>
                <h2>Assign Truck to branch</h2>
            </center>
                <form onSubmit={e=>handleSubmit(e)}>
                    <label>Select Branch</label>
                    <center><select id="branches">
                    {branches !=='' ? branches.map((branch)=>{
                        return (
                            <option value={branch.id}>{branch.name}</option>
                            )
                    }):null}
                    </select></center>
                    <label>Truck Name</label>
                    <center><input type="text" placeholder="Name" id="name" /></center>
                    <center><input type="submit" value="Assign Truck" className="submit-btn"/></center>
                </form>
                <Link to="/admin" className="back-btn"> Back</Link>
                <center><p id="error-message"></p></center>
        </div>
    )
}
export default BranchCreate;