import React,{useState,useEffect} from 'react';
import './styles.css';
import axios from 'axios';
import {Link} from 'react-router-dom'
const Trucks = ()=>{
    const [trucks,setTrucks] = useState('')
    useEffect(()=>{
        if(window.localStorage.getItem('token')){
            axios.get('http://127.0.0.1:8000/branch/trucks/',{ headers: {Authorization: 'Bearer ' + window.localStorage.getItem('token')}})
            .then(resp=>{
                console.log(resp.data)
                setTrucks(resp.data)
            })
            .catch(errors =>{
                console.log("errors")
            })
        }
    },[])
    const deleteBranch = (id)=>{
        axios.delete(`http://127.0.0.1:8000/branch/truck/${id}`,{ headers: {Authorization: 'Bearer ' + window.localStorage.getItem('token')}})
            .then(resp=>{
                window.location = '/admin'
            })
            .catch(errors =>{
                console.log("errors")
            })
    }
    const renderData = ()=>{
        return (
            <table>
                <tbody>
                    <tr>
                        <th>Id</th>
                        <th>Truck Name</th>
                        <th>Branch Name</th>
                        <th></th>
                    </tr>
                {trucks.map((truck)=>{
                    return (
                        <tr key={truck.id}>
                            <td>{truck.id}</td>
                            <td>{truck.name}</td>
                            <td>{truck.branch.name}</td>
                            <td><button className='delete-btn' onClick={()=>deleteBranch(truck.id)}>Delete</button></td>
                        </tr>
                        )
                })}
                </tbody>
            </table>
        )
    }
    return (
        <div className="center-content">
            <center><Link to="/admin/truck-assign"><button className="add-btn" >Add </button></Link></center>
            {trucks === '' ? <div style={{'marginTop':'10px'}}>No Trucks</div> : renderData()}
        </div>
    )
}
export default Trucks;